<template>
  <div class="operationPane">
    <!-- -->
    <!-- -->
    <div
      class="operationPane_con"
      style="display: flex; justify-content: center; align-items: flex-end"
      v-show="szMainShow"
    >
      <div class="operationPane_con_uppershaft">
        <div class="pch" v-show="issaoma"><input v-model="pch" /></div>
        <div class="chooseBtn">
          <div class="chooseBtn_con">
            <div class="chooseBtn_con_label"><span></span></div>
            <div class="chooseBtn_con_btn" @click="toChooseMachine()">
              <span v-show="!isCheckedMachine"></span
              ><span v-show="isCheckedMachine">{{ checkedMachineNum }}</span>
            </div>
          </div>
          <div class="chooseBtn_con">
            <div class="chooseBtn_con_label"><span>A&B</span></div>
            <div class="chooseBtn_con_btn" @click="toShift()">
              <span></span>
            </div>
          </div>
        </div>
        <div class="pane">
          <div class="text">
            <div class="text_con">
              <span>....</span><span>....</span>
            </div>
            <div class="text_con"><span>....</span></div>
          </div>
          <div class="pane_title"><span></span></div>
          <div class="pane_con">
            <textarea
              style="background: white"
              class="textarea"
              disabled
              placeholder="..."
            ></textarea>
          </div>
          <div class="pane_btn">
            <div
              class="pane_btn_one"
              @click="saoma()"
              :style="issaoma ? 'background:#808080' : 'background:#A3D897'"
            >
              <span></span>
            </div>
            <div
              class="pane_btn_one"
              @click="wanchen()"
              :style="issaoma ? 'background:#A3D897' : 'background:#808080'"
            >
              <span></span>
            </div>
          </div>
        </div>
      </div>
      <img src="../../static/img/close.png" @click="closeCurrentPage" />
    </div>
    <!-- -->
    <!-- -->
    <div
      class="operationPane_con"
      style="display: flex; justify-content: center; align-items: flex-start"
      v-show="szMachineShow"
    >
      <div class="operationPane_con_machineList">
        <el-checkbox-group
          :max="1"
          @change="checkedMachine"
          v-model="checkMachine"
          style="width: 100%; height: 100%"
        >
          <el-checkbox-button
            size="medium"
            v-for="(item, index) in machineList"
            style="magin: 1rem; font-size: 2rem"
            :label="item.machine_id"
            :key="index"
            >{{ item.machine_id }}</el-checkbox-button
          >
        </el-checkbox-group>
      </div>
      <div class="operationPane_con_machineList_btn">
        <div class="operationPane_con_machineList_btn_left">
          <div class="operationPane_con_machineList_btn_leftBtn" @click="sureMachine()">
            
          </div>
          <div
            class="operationPane_con_machineList_btn_leftBtn"
            style=""
            @click="cancel()"
          >
            
          </div>
        </div>
        <div class="operationPane_con_machineList_btn_right">
          <el-pagination
            background
            small
            layout="prev, pager, next"
            :pager-count="3"
            :total="total_num"
            :page-size="21"
            @current-change="CurrentNameChange2"
          >
          </el-pagination>
        </div>
      </div>
      <!-- <div class="leftLabel"><span></span></div> -->
      <div class="search" style="left: 4rem; width: 95%; top: 18px">
        <span style="font-size: 1.7rem"></span
        ><input placeholder="" v-model="search_machine" />
        <div class="checked_machine_btn_one" style="height: 3rem" @click="search()">
          
        </div>
        <span style="color: red; margin-left: 1rem"
          >{{ this.checkedMachineNum }}</span
        >
      </div>
      <img src="../../static/img/close.png" @click="closeCurrentPage" />
    </div>
    <!-- -->
    <!-- -->
    <div
      class="operationPane_con"
      style="display: flex; justify-content: center; align-items: flex-start"
      v-show="szShiftShow"
    >
      <div class="operationPane_con_machineList">
        <div class="shift">
          <div class="shift_con">
            <span></span
            ><select
              :disabled="!isStartChange"
              v-model="className"
              @change="classnameChange"
            >
              <option value="6">A</option>
              <option value="7">B</option>
              <option value="8">C</option>
            </select>
          </div>
          <div class="shift_con" v-for="(item, index) in staffList" :key="index">
            <span>{{ item.label }}</span>
            <div
              :class="item.isSelected ? 'staffCheck2' : 'staffCheck'"
              @click="chooseStaff(item.label)"
            >
              {{ item.staffName }}
            </div>
          </div>
        </div>
        <el-checkbox-group
          :max="1"
          :disabled="!isStartChange"
          text-color="#ffffff"
          fill="#3296FA"
          v-model="checkStaffName"
          @change="checkedName"
          style="width: 100%; height: 100%"
        >
          <el-checkbox-button
            size="medium"
            v-for="(item, index) in StaffNameList"
            style="magin: 1rem; font-size: 2rem"
            :label="item.staff_name"
            :key="index"
            >{{ item.staff_name }}</el-checkbox-button
          >
        </el-checkbox-group>
      </div>
      <div class="operationPane_con_machineList_btn">
        <div class="operationPane_con_machineList_btn_left">
          <div
            class="operationPane_con_machineList_btn_leftBtn"
            @click="startChange()"
            :style="isStartChange ? 'background:#8C8C8C' : 'background:#A3D897'"
          >
            
          </div>
          <div
            class="operationPane_con_machineList_btn_leftBtn"
            :style="isStartChange ? 'background:#A3D897;' : 'background:#8C8C8C;'"
            @click="save()"
          >
            
          </div>
        </div>
        <div class="operationPane_con_machineList_btn_right">
          <el-pagination
            background
            small
            layout="prev, pager, next"
            :pager-count="4"
            @current-change="CurrentNameChange"
            :total="total_num2"
            :page-size="12"
          >
          </el-pagination>
        </div>
      </div>
      <div class="leftLabel"><span></span></div>

      <img src="../../static/img/close.png" @click="closeCurrentPage" />
    </div>
    <!-- -->
    <!-- -->
  </div>
</template>

<script>
let host = "http://120.55.124.53:12140";
import axios from "axios";
export default {
  name: "illustration",
  data() {
    return {
      company_id: "10000025",
      szMainShow: true, //
      szMachineShow: false, //
      szShiftShow: false, //
      checkMachine: [], //
      checkStaffName: [], //
      checkedMachineNum: "", //
      isCheckedMachine: false, //
      isStartChange: false, //
      issaoma: false,
      staffList: [
        {
          label: "01",
          staffName: "",
          isSelected: false,
          id: null,
        },
        {
          label: "02",
          staffName: "",
          isSelected: false,
          id: null,
        },
        {
          label: "03",
          staffName: "",
          isSelected: false,
          id: null,
        },
        {
          label: "04",
          staffName: "",
          isSelected: false,
          id: null,
        },
      ],
      StaffNameList: [],
      machineList: [], //
      pch: "SZ2020010240001-1",
      total_num: null,
      page_size: 16,
      page_num: 1,
      total_num2: null,
      page_size2: 12,
      page_num2: 1,
      mac_type_id: "030100",
      className: "6",
      search_machine: "",
    };
  },
  methods: {
    search() {
      this.getMachineList(this.search_machine);
    },
    classnameChange(e) {
      //console.log(e.target.value);
      if (e.target.value == "6") {
        this.getcurrentGroup(15, "A");
      } else if (e.target.value == "7") {
        this.getcurrentGroup(15, "B");
      } else if (e.target.value == "8") {
        this.getcurrentGroup(15, "C");
      }
    },
    getcurrentGroup(id, className) {
      let url2 = host + "/api/group/getGroupDetail";
      let that = this;
      that.staffList = [];
      axios({
        url: url2,
        method: "post",
        data: {
          selectInfo: {
            company_id: that.company_id,
          },
          shiftGroup: {
            id: id,
          },
        },

        // headers: headers
      }).then((res) => {
        //console.log(res);
        if (className == "A") {
          res.data.result.forEach((element) => {
            if (element.group_name == "A") {
              element.staffList = that.sortByKey(element.staffList, "order_num");
              element.staffList.forEach((elements) => {
                if (elements.order_num == "1") {
                  that.staffList.push({
                    label: "01",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "2") {
                  that.staffList.push({
                    label: "02",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "3") {
                  that.staffList.push({
                    label: "03",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "4") {
                  that.staffList.push({
                    label: "04",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                }
              });
            }
          });
        } else if (className == "B") {
          res.data.result.forEach((element) => {
            if (element.group_name == "B") {
              element.staffList = that.sortByKey(element.staffList, "order_num");
              element.staffList.forEach((elements) => {
                if (elements.order_num == "1") {
                  that.staffList.push({
                    label: "01",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "2") {
                  that.staffList.push({
                    label: "02",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "3") {
                  that.staffList.push({
                    label: "03",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "4") {
                  that.staffList.push({
                    label: "04",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                }
              });
            }
          });
        }
        if (className == "C") {
          res.data.result.forEach((element) => {
            if (element.group_name == "C") {
              element.staffList = that.sortByKey(element.staffList, "order_num");
              element.staffList.forEach((elements) => {
                if (elements.order_num == "1") {
                  that.staffList.push({
                    label: "01",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "2") {
                  that.staffList.push({
                    label: "02",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "3") {
                  that.staffList.push({
                    label: "03",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                } else if (elements.order_num == "4") {
                  that.staffList.push({
                    label: "04",
                    staffName: elements.staff_name,
                    isSelected: false,
                    id: elements.id,
                  });
                }
              });
            }
          });
        }
        //console.log(that.staffList);
      });
    },
    // 
    sortByKey(array, key) {
      return array.sort(function (a, b) {
        let x = a[key];
        let y = b[key];
        return x < y ? -1 : x > y ? 1 : 0;
      });
    },
    getMachineList(machine_id) {
      //
      let data = {};
      if (machine_id) {
        data = {
          selectInfo: {
            company_id: this.company_id,
            page_size: this.page_size,
            page_num: this.page_num,
          },
          mac_type_id: this.mac_type_id,
          machine_id: machine_id,
        };
      } else {
        data = {
          selectInfo: {
            company_id: this.company_id,
            page_size: this.page_size,
            page_num: this.page_num,
          },
          mac_type_id: this.mac_type_id,
        };
      }
      let that = this;
      let url = host + "/api/stationMachine/getMachines";
      let method = axios({
        url: url,
        method: "post",
        data: data,
      }).then((response) => {
        that.total_num = response.data.result.total_num;
        let array = response.data.result.models;
        that.machineList = [];
        array.forEach((element) => {
          that.machineList.push(element);
        });
      });
    },
    CurrentNameChange2(e) {
      //machinelist
      //console.log(e);
      this.page_num = e;
      this.getMachineList();
    },
    CurrentNameChange(e) {
      //namelist
      //console.log(e);
      this.page_num2 = e;
      this.getStaffList();
    },
    getStaffList() {
      //
      let url = "http://106.12.219.66:8227/report/getSimpleReport";
      let data = {
        tableName: "s_staff",
        sort: "DESC",
        sortCloumn: "id",
        selectFields: ["id", "staff_name"],
        pageNum: this.page_num2,
        pageSize: this.page_size2,
        query: {
          staff_organization_id: 38, //
        },
      };
      let headers = {
        companyId: this.company_id,
      };
      this.StaffNameList = [];
      let that = this;
      axios({
        url: url,
        method: "post",
        headers: headers,
        data: data,

        // headers: headers
      }).then((res) => {
        //console.log(res);
        let arr = res.data.data;
        for (let i = 0; i < arr.length; i++) {
          that.StaffNameList.push(arr[i]);
        }
        that.total_num2 = res.data.total;
        //   //console.log(that.total_num2)
      });
      // let url = "http://120.55.124.53:8206/api/staff/getStaffListByOrganization"
      // let data = {
      //   "page": this.page_num2,
      //   "pageNum": this.page_size2,
      //   "staff_organization_id": 1,
      //   "query_condition": ""
      // }
      // this.NameList = []
      // let that = this
      // axios({
      //     url: url,
      //     method: "post",
      //     headers: {
      //       companyID: that.company_id
      //     },
      //     data: data,

      //     // headers: headers
      //   })
      //   .then(res => {

      //     let arr = res.data.data.staffModel
      //     for (let i = 0; i < arr.length; i++) {
      //       that.StaffNameList.push(arr[i])
      //     }
      //     that.total_num2 = res.data.totalDataNum

      //   })
    },
    closeCurrentPage() {
      //console.log(this.szMachineShow);
      if (this.szMachineShow == true) {
        this.isCheckedMachine = false;
        this.checkedMachineNum = "";
        this.checkMachine = [];
        this.szMachineShow = false;
        this.szShiftShow = false;
        this.szMainShow = true;
        return;
      }
      if (this.szShiftShow == true) {
        this.szMachineShow = false;
        this.szShiftShow = false;
        this.szMainShow = true;
        this.isStartChange = false;
        return;
      }
    },
    saoma() {
      if (this.issaoma == false) {
        if (this.checkedMachineNum == "") {
          this.$message({
            message: "",
            type: "warning",
          });
        } else {
          //console.log(this.staffList);
          let staffList = [];
          this.staffList.forEach((element) => {
            staffList.push(element.staffName);
          });
          let url = host + "/api/stationMachine/caPian";

          let method = "post";
          let data = {
            selectInfo: {
              company_id: this.company_id,
            },
            machine_id: this.checkedMachineNum,
            ca_pian_ren: String(staffList),
          };

          let that = this;

          axios({
            url: url,
            method: method,
            data: data,
          }).then((response) => {
            if (response.data.message == "") {
              this.$message({
                message: "",
                type: "success",
              });
              this.issaoma = true;
            } else {
              this.$message.error(response.data.message);
            }
          });
        }
      }
    },
    wanchen() {
      if (this.issaoma == true) {
        if (this.checkedMachineNum == "") {
          this.$message({
            message: "",
            type: "warning",
          });
        } else {
          //console.log(this.staffList);
          let staffList = [];
          this.staffList.forEach((element) => {
            staffList.push(element.staffName);
          });
          let url = host + "/api/stationMachine/caPian";

          let method = "post";
          let data = {
            selectInfo: {
              company_id: this.company_id,
            },
            machine_id: this.checkedMachineNum,
            ca_pian_ren: String(staffList),
          };

          let that = this;

          axios({
            url: url,
            method: method,
            data: data,
          }).then((response) => {
            if (response.data.message == "") {
              this.$message({
                message: "",
                type: "success",
              });
              this.issaoma = false;
            } else {
              this.$message.error(response.data.message);
            }
          });
        }
      }
    },
    toChooseMachine() {
      this.szMainShow = false;
      this.szShiftShow = false;
      this.szMachineShow = true;
    },
    toShift() {
      this.szMainShow = false;
      this.szMachineShow = false;
      this.szShiftShow = true;
    },
    sureMachine() {
      if (typeof this.checkedMachineNum == "undefined") {
        this.isCheckedMachine = false;
      } else {
        this.isCheckedMachine = true;
      }
      this.checkMachine = [];
      this.szMachineShow = false;
      this.szShiftShow = false;
      this.szMainShow = true;
    },
    cancel() {
      //
      this.isCheckedMachine = false;
      this.checkedMachineNum = "";
      this.checkMachine = [];
      this.szMachineShow = false;
      this.szShiftShow = false;
      this.szMainShow = true;
    },
    save() {
      //
      if (this.isStartChange == true) {
        let url = host + "/api/group/shift";
        let staffList = [];
        for (let i = 0; i < this.staffList.length; i++) {
          staffList.push({
            id: this.staffList[i].id,
            order_num: i + 1,
          });
        }

        let data = {
          selectInfo: {
            company_id: this.company_id,
          },
          shiftGroup: {
            id: parseInt(this.className),
          },
          staffList: staffList,
        };
        //console.log(data);

        let that = this;
        axios({
          url: url,
          method: "post",

          data: data,

          // headers: headers
        }).then((res) => {
          if (res.data.message == "") {
            this.$message({
              message: "",
              type: "success",
            });
          } else {
            this.$message.error("");
          }
          this.checkStaffName = [];
          this.szMachineShow = false;
          this.szShiftShow = false;
          this.szMainShow = true;
          this.isStartChange = false;
          this.$emit("cpChange", this.staffList);
        });
        this.className = "6";
        //console.log(staffList);
      }
    },
    startChange() {
      //
      this.isStartChange = true;
    },
    checkedMachine(e) {
      //

      this.checkedMachineNum = e[0];
    },
    checkedName(e) {
      //
      for (let i = 0; i < this.staffList.length; i++) {
        if (this.staffList[i].isSelected == true) {
          this.staffList[i].staffName = e[0];
          let url = "http://106.12.219.66:8227/report/getSimpleReport";
          let headers = {
            "Content-Type": "application/json",
            companyID: this.company_id,
          };
          let method = "post";
          let data = {
            tableName: "s_staff",
            pageNum: 1,
            pageSize: 1000,
            query: {
              staff_name: e[0],
            },
          };

          let that = this;

          axios({
            url: url,
            method: method,
            data: data,
            headers: headers,
          }).then((response) => {
            //console.log(response);
            this.staffList[i].id = response.data.data[0].id; //id
            this.checkStaffName = [];
          });
        }
      }
      this.checkStaffName = [];
    },
    chooseStaff(label) {
      //
      if (this.isStartChange == true) {
        for (let i = 0; i < this.staffList.length; i++) {
          this.staffList[i].isSelected = false;
        }
        for (let i = 0; i < this.staffList.length; i++) {
          if (this.staffList[i].label == label) {
            this.staffList[i].isSelected = true;
          }
        }
      }
    },
  },
  mounted() {},
  watch: {
    szShiftShow(val) {
      if (val == true) {
        this.page_num2 = 1;
        this.getStaffList();
        this.getcurrentGroup(15, "A");
      }
    },
    szMachineShow(val) {
      if (val == true) {
        this.page_num2 = 1;
        this.getMachineList();
        this.search_machine = "";
      }
    },
  },
};
</script>

<style>
.pch {
  position: absolute;
  left: 1rem;
  top: -3.5rem;
  width: 15.5rem;
  height: 3rem;
}

.pch input {
  width: 100%;
  height: 100%;
  border: 1px solid black;
  font-size: 1.5rem;
}

.pch input::-webkit-input-placeholder {
  font-size: 1.3rem;
}

.operationPane {
  width: 100%;
  height: 64%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  background: #e2e7ed;
}

.operationPane_con {
  width: 98%;
  height: 96%;
  background: white;
  border-radius: 16px;
  position: relative;
}

.operationPane_con img {
  position: absolute;
  right: 0;
  top: -8px;
  width: 63px;
  height: 63px;
}

.search {
  position: absolute;
  left: 14%;
  top: 10px;

  font-size: 1.7rem;
  width: 69%;
  height: 100px;

  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.search input {
  border: 1px solid black;
  height: 3rem;
  font-size: 1.7rem;
}
.checked_machine_btn_one {
  width: 18%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  margin-left: 0.2rem;
  margin-right: 0.2rem;
  background: #a3d897;
  border-top: 3px solid #ffffff;
  border-left: 3px solid #ffffff;
  border-bottom: 3px solid #717171;
  border-right: 3px solid #717171;
}

.leftLabel {
  position: absolute;
  left: 0;
  top: 0px;
  border-top-left-radius: 16px;
  font-size: 1.7rem;
  width: 135px;
  height: 63px;
  background: #d4d4d4;
  display: flex;
  align-items: center;
  justify-content: center;
}

.operationPane_con_uppershaft {
  width: 85%;
  height: 70%;
  margin-bottom: 3%;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.text {
  position: absolute;
  top: -20%;
